// Import React
import React from "react"

// Import Spectacle Core tags
import {
  BlockQuote,
  Cite,
  Deck,
  Heading,
  ListItem,
  List,
  Quote,
  Slide,
  Text,
  CodePane,
  Notes,
  Image,
  Appear,
  TableRow,
  Table,
  TableHeaderItem,
  TableItem,
  TableBody,
  TableHeader
} from "spectacle"

// Import theme
import createTheme from "spectacle/lib/themes/default"

import provaCode from "./code-examples/prova"
import interopCode from "./code-examples/interop"
import reprocessingCode from "./code-examples/reprocessing"
import statelessCode from "./code-examples/stateless-component"
import statelessCompiled from "./code-examples/stateless-compiled"
import reducerComponent from "./code-examples/reducer-component"

// Require CSS
require("normalize.css")
require("./themes/prism-atom-dark.css")
require("prismjs/components/prism-reason")

const theme = createTheme(
  {
    primary: "white",
    secondary: "#fc6e55", // tomato
    tertiary: "#1F2022", // ~black
    quartenary: "black",
    dark: "#1D1F21",
    grey: "#ADADAD"
  },
  {
    primary: "Montserrat",
    secondary: "Helvetica"
  }
)

class Row extends React.Component {
  render() {
    return (
      <div
        style={{
          display: "flex",
          alignItems: "center",
          ...this.props.style
        }}
      >
        {this.props.children}
      </div>
    )
  }
}

export default class Presentation extends React.Component {
  render() {
    return (
      <Deck transition={["fade"]} transitionDuration={200} theme={theme}>
        {/* --- Homepage --- */}
        <Slide controlColor="grey" progressColor="grey">
          <Notes>
            Ciao! Oggi vi parler di un linguaggio di programmazione ReasonML,
            ma prima, una breve introduzione.
          </Notes>

          <Heading size={3} lineHeight={1} textColor="secondary" bold>
            ReasonML: enjoy front-end programming again
          </Heading>

          <Heading size={4} margin={64}>
            Alberto Nicoletti
          </Heading>
        </Slide>
        {/* --- About me --- */}
        <Slide controlColor="grey" progressColor="grey">
          <Notes>
            Due parole su di me: studio informatica al corso di laurea
            magistrale all'universita' di Bologna e sono co-fondatore di
            Braimage, dove stiamo lavorando ad un software di intelligenza
            artificiale per semplificare il percorso di diagnosi della demenza.
          </Notes>
          <Heading margin={"64px 0px"} textColor="secondary">
            About me
          </Heading>
          <Row>
            <Image
              alt="unibo"
              src={require("./images/unibo.png")}
              width={300}
            />
            <Image
              alt="braimage"
              src={require("./images/braimage.svg")}
              width={400}
            />
          </Row>
        </Slide>
        {/* --- State of JS --- */}
        <Slide bgColor="#212424" controlColor="grey" progressColor="grey">
          <Heading fit textColor="secondary">
            The State of JS 2018
          </Heading>
          <Image src={require("./images/state-of-js.png")} />
          <Image src={require("./images/state-of-js-2.png")} />
        </Slide>
        {/* --- What's Reason --- */}
        <Slide controlColor="grey" progressColor="grey">
          <Notes>
            Quindi, innanzitutto, cos'e' Reason? E' un linguaggio funzionale
            creato a Facebook circa 2 anni fa
          </Notes>
          <Image alt="reason" src={require("./images/reason.png")} />
          <List textAlign="center">
            <Text style={{ lineHeight: 1.2 }}>Created by Facebook in 2016</Text>
            <Text style={{ lineHeight: 1.2 }}>
              Functional programming language
            </Text>
            <Text style={{ lineHeight: 1.2 }}>Compiles to Javascript</Text>
          </List>
        </Slide>
        {/* --- Reason is not a new language --- */}
        <Slide controlColor="grey" progressColor="grey">
          <Heading textColor="secondary">Reason is not a new language</Heading>
          <Appear>
            <div>
              <Image
                alt="ocaml"
                src={require("./images/ocaml.png")}
                margin={"64px auto 0px auto"}
                width={200}
                height={200}
              />
              <Heading size={4}>OCaml</Heading>
            </div>
          </Appear>
        </Slide>
        {/* --- OCaml --- */}
        <Slide controlColor="grey" progressColor="grey">
          <Heading textColor="secondary">OCaml</Heading>
          <List textAlign="center">
            <Text style={{ lineHeight: 1.2, fontSize: "3.5rem" }}>
              Created in 1996
            </Text>
            <Text style={{ lineHeight: 1.2, fontSize: "3.5rem" }}>
              Run fast, compile faster
            </Text>
            <Text style={{ lineHeight: 1.2, fontSize: "3.5rem" }}>
              Sound type system
            </Text>
            <Text style={{ lineHeight: 1.2, fontSize: "3.5rem" }}>
              Type inference
            </Text>
          </List>
        </Slide>
        {/* --- Hindley Milner --- */}
        <Slide controlColor="grey" progressColor="grey">
          <Heading size={3} textColor="secondary">
            Hindley Milner type system
          </Heading>
          <Row>
            <Image
              alt="hindley-milner"
              src={require("./images/hindley-milner.png")}
              margin={"32px 64px"}
            />
            <div>
              <Heading size={5}>No type annotation required!</Heading>
              <Heading size={5} margin={"32px"}>
                No types at runtime!
              </Heading>
            </div>
          </Row>
        </Slide>
        {/* --- Bucklescript --- */}
        <Slide controlColor="grey" progressColor="grey">
          <Heading textColor="secondary" margin="64px 0px">
            Bucklescript
          </Heading>
          <Heading size={4}>OCaml  JS</Heading>
        </Slide>
        {/* --- So what's Reason? --- */}
        <Slide bgColor="tertiary" controlColor="grey" progressColor="grey">
          <Heading textColor="primary" fit margin="64px 0px">
            So what's Reason exactly?
          </Heading>
          <img src={require("./images/thinking.png")} width={250} />
        </Slide>
        {/* --- New syntax and toolchain --- */}
        <Slide controlColor="grey" progressColor="grey" textColor="secondary">
          <Row>
            <BlockQuote>
              <Quote
                textSize={54}
                textColor="secondary"
                style={{ borderLeft: "1px solid #fc6e55" }}
              >
                It's a new syntax and toolchain powered by OCaml
              </Quote>
            </BlockQuote>
            <div>
              <Text style={{ lineHeight: 1.2, fontSize: "3.5rem" }}>
                Reason
              </Text>
              <Text>
                <span role="img"></span>
              </Text>
              <Text style={{ lineHeight: 1.2, fontSize: "3.5rem" }}>OCaml</Text>
              <Text></Text>
              <Text style={{ lineHeight: 1.2, fontSize: "3.5rem" }}>
                Bucklescript
              </Text>
              <Text></Text>
              <Text style={{ lineHeight: 1.2, fontSize: "3.5rem" }}>
                Javascript
              </Text>
            </div>
          </Row>
        </Slide>
        {/* --- Why not Typescript? --- */}
        {/* <Slide bgColor="white" controlColor="grey" progressColor="grey">
          <Notes />
          <Heading textColor="primary" padding={32} size={2}>
            Why not Typescript of Flow?
          </Heading>
        </Slide> */}
        {/* Why not Elm? */}
        {/* <Slide bgColor="white" controlColor="grey" progressColor="grey">
          <Notes />
          <Heading textColor="primary" padding={32} size={2}>
            Why not Elm?
          </Heading>
        </Slide> */}
        {/* --- Alternatives --- */}
        <Slide controlColor="grey" progressColor="grey" textColor="tertiary">
          <Heading textColor="secondary" padding={32} size={4}>
            Alternatives
          </Heading>
          <Table className="alternatives-table">
            <TableHeader bold>
              <TableRow>
                <TableHeaderItem />
                <TableHeaderItem>Type safety</TableHeaderItem>
                <TableHeaderItem>JS interop</TableHeaderItem>
              </TableRow>
            </TableHeader>
            <TableBody>
              <TableRow>
                <TableItem>Typescript</TableItem>
                <TableItem>Not quite safe</TableItem>
                <TableItem>Very easy</TableItem>
              </TableRow>
              <TableRow>
                <TableItem>Elm</TableItem>
                <TableItem>100%</TableItem>
                <TableItem>Cumbersome</TableItem>
              </TableRow>
              <TableRow>
                <TableItem>Reason</TableItem>
                <TableItem>Reasonably safe</TableItem>
                <TableItem>Easy enougha</TableItem>
              </TableRow>
            </TableBody>
          </Table>
        </Slide>
        {/* --- Reason vs Javascript syntax --- */}
        <Slide bgColor="dark" controlColor="grey" progressColor="grey">
          <Table textAlign="left" className="dark-table">
            <TableHeader bold>
              <TableRow>
                <TableHeaderItem />
                <TableHeaderItem>Javascript</TableHeaderItem>
                <TableHeaderItem>Reason</TableHeaderItem>
              </TableRow>
            </TableHeader>
            <TableBody>
              <TableRow>
                <TableItem>Constant</TableItem>
                <TableItem>
                  <CodePane
                    lang="javascript"
                    source="const x = 5"
                    textSize="24px"
                    theme="external"
                  />
                </TableItem>
                <TableItem>
                  <CodePane
                    lang="reason"
                    textSize="24px"
                    theme="external"
                    source="let x = 5"
                  />
                </TableItem>
              </TableRow>
              <TableRow>
                <TableItem>Named function</TableItem>
                <TableItem>
                  <CodePane
                    lang="javascript"
                    textSize="24px"
                    theme="external"
                    source="function foo(x) { ... }"
                  />
                </TableItem>
                <TableItem>
                  <CodePane
                    lang="reason"
                    textSize="24px"
                    theme="external"
                    source="let foo = (x) => ..."
                  />
                </TableItem>
              </TableRow>
              <TableRow>
                <TableItem>Lambda function</TableItem>
                <TableItem>
                  <CodePane
                    lang="javascript"
                    textSize="24px"
                    theme="external"
                    source="x => x + 1"
                  />
                </TableItem>
                <TableItem>
                  <CodePane
                    lang="reason"
                    textSize="24px"
                    theme="external"
                    source="(x) => x + 1"
                  />
                </TableItem>
              </TableRow>
              <TableRow>
                <TableItem>
                  Type <br /> Definition
                </TableItem>
                <TableItem>
                  Missing{" "}
                  <span role="img" aria-label="sad face">
                    
                  </span>
                </TableItem>
                <TableItem>
                  <CodePane
                    lang="reason"
                    textSize="24px"
                    theme="external"
                    source="type point = { x: int, y: int }"
                  />
                </TableItem>
              </TableRow>
              <TableRow>
                <TableItem>Record</TableItem>
                <TableItem>
                  <CodePane
                    lang="javascript"
                    textSize="24px"
                    theme="external"
                    source="{ x: 0, y: 0 }"
                  />
                </TableItem>
                <TableItem>
                  <CodePane
                    lang="reason"
                    textSize="24px"
                    theme="external"
                    source="{ x: 0, y: 0 } "
                  />
                </TableItem>
              </TableRow>
              <TableRow>
                <TableItem>
                  Immutable
                  <br />
                  List
                </TableItem>
                <TableItem>
                  Missing{" "}
                  <span role="img" aria-label="sad face">
                    
                  </span>
                </TableItem>
                <TableItem>
                  <CodePane
                    lang="reason"
                    textSize="24px"
                    theme="external"
                    source="[ 1, 2, 3 ]"
                  />
                </TableItem>
              </TableRow>
              <TableRow>
                <TableItem>Array</TableItem>
                <TableItem>
                  <CodePane
                    lang="javascript"
                    textSize="24px"
                    theme="external"
                    source="[ 1, 2, 3 ]"
                  />
                </TableItem>
                <TableItem>
                  <CodePane
                    lang="reason"
                    textSize="24px"
                    theme="external"
                    source="[| 1, 2, 3 |]"
                  />
                </TableItem>
              </TableRow>
            </TableBody>
          </Table>
        </Slide>
        {/* --- Pattern Matching --- */}
        <Slide bgColor="dark" controlColor="grey" progressColor="grey">
          <Heading size={1} margin={"42px 0px"} fit textColor="secondary">
            Pattern Matching
          </Heading>
          <CodePane
            theme="external"
            lang="reason"
            source={provaCode}
            textSize="26px"
          />
        </Slide>
        {/* --- Pipes --- */}
        {/* <Slide bgColor="dark" controlColor="grey" progressColor="grey">
          <Heading size={2} margin={"42px 0px"} caps textColor="primary">
            Pipes
          </Heading>
          <CodePane
            theme="external"
            lang="reason"
            source={provaCode}
            textSize="26px"
          />
        </Slide> */}
        {/* --- Javascript interop --- */}
        <Slide bgColor="dark" controlColor="grey" progressColor="grey">
          <Heading size={2} margin={"42px 0px"} textColor="secondary" fit>
            Javascript interoperability
          </Heading>
          <CodePane
            theme="external"
            lang="reason"
            source={interopCode}
            textSize="21px"
          />
        </Slide>
        {/* --- */}
        {/* <Slide bgColor="primary">
          <Heading size={2} textColor="tertiary">
            Abstractions
          </Heading>
          <List>
            <ListItem>Functors</ListItem>
            <ListItem>Polymorphic Variants</ListItem>
            <ListItem>Generalized Algebraic Data Types</ListItem>
            <ListItem>First Class Modules</ListItem>
            <ListItem>...</ListItem>
          </List>
        </Slide> */}
        {/* --- Reason React --- */}
        <Slide controlColor="grey" progressColor="grey">
          <Notes>React  stato scritto in OCaml</Notes>
          <Heading textColor="secondary">ReasonReact</Heading>
          <Text>Immutability and Types at language level</Text>
        </Slide>
        {/* --- Stateless Component */}
        <Slide bgColor="dark" controlColor="grey" progressColor="grey">
          <Heading textColor="secondary" fit margin="64px 0px">
            Stateless Component
          </Heading>
          <CodePane
            source={statelessCode}
            lang="reason"
            theme="external"
            textSize="20px"
          />
          <Text textColor="primary">Compiles to:</Text>
          <CodePane
            source={statelessCompiled}
            lang="javascript"
            theme="external"
            textSize="20px"
          />
        </Slide>
        {/* --- Reducer component --- */}
        <Slide bgColor="dark" controlColor="grey" progressColor="grey">
          <Heading fit textColor="secondary">
            Reducer Component
          </Heading>
          <CodePane
            source={reducerComponent}
            lang="reason"
            theme="external"
            textSize="18px"
          />
        </Slide>
        {/* --- Reprocessing --- */}
        <Slide controlColor="grey" progressColor="grey">
          <Heading textColor="secondary">Reprocessing</Heading>
          <Row style={{ marginTop: 40 }}>
            <CodePane
              source={reprocessingCode}
              lang="reason"
              theme="external"
              textSize="21px"
            />
            <Image
              alt="reprocessing"
              src={require("./images/reprocessing.png")}
              width={211}
              margin="0px 32px"
            />
          </Row>
        </Slide>
        <Slide controlColor="grey" progressColor="grey">
          <Heading textColor="secondary" margin="64px 0px">
            Gentype
          </Heading>
          <Text>Generate types for Typescript/Flow codebases</Text>
        </Slide>
        <Slide controlColor="grey" progressColor="grey">
          <Heading textColor="secondary">Thank you!</Heading>
        </Slide>
      </Deck>
    )
  }
}
